<template>
    <div class="container"
         :class="{small: small}"
    >
        <button class="button"
                :class="[{inactive : !active}, {small: small},{disabled : clicked === true}, color]"
                @click="clickBtn"
        >
            <slot></slot>
        </button>
    </div>
</template>

<script>
    export default {
        name: "MainButton",
        props: {
            color: {
                type: String,
                default: "blue",
            },
            active: {
                type: Boolean,
                default: true,
            },
            small: {
                type: Boolean,
                required: false,
                default: false,
            },
        },
        data() {
            return {
                clicked: false,
                windowBottom: false,
            };
        },
        methods: {
            clickBtn() {
                this.clicked = true;
                setTimeout(() => this.clicked = false, 2000);
            },
        },
    };
</script>

<style scoped>
.container.small {
  width: calc(50% - 4px);
}

.button {
  width: 100%;
  display: flex;
  justify-content: center;
  padding: 16px;
  font-weight: 600;
  font-size: 16px;
  line-height: 20px;
  letter-spacing: -0.02em;
  font-family: "SF Pro", serif;
  color: #FFFFFF;
  border-radius: 14px;
  border: unset;
  background: #1D86FF;
  transition: filter .25s ease-in-out;
}

.button.small {
  padding: 11px 0;
  font-weight: 500;
}

.button.gray {
  background: #F2F4F7;
  color: #1D86FF;
}

.button.gray.inactive {
  color: #9B9FA9;
  pointer-events: none;
}

.button.disabled {
  pointer-events: none;
}

.btn.disabled .button {
  color: #9B9FA9;
}

.button.shadow {
  filter: drop-shadow(0px 2px 10px rgba(6, 16, 41, 0.1));
}

</style>